import { SVG_convert } from "./SVG_converter";

let http = require("http");

console.log("start\n");

let server = http.createServer(function(req, res) {
    console.log("connected");
    if (req.method == "POST") {
        let body = "";
        // data(chunk)
        // body 
        req.on("data", function(chunk) {
            body += chunk;
        });

        // (end)
        req.on("end", function() {
            let result = SVG_convert(body);
            console.log("finish");
            res.end( "\n" + result + "\n");
        });
    }
}).listen(1337, "0.0.0.0");